import { Component } from "react";

class Counter4 extends Component {
  // constructor   
  state = {
    number: 0,
    fixedNumber: 0,
  };
  render() {
    const { number, fixedNumber } = this.state;
    return (
      <div>
        <h1>{number}</h1>
        <h2>   : {fixedNumber}</h2>
        <button
          // Counter2.js        2 
          onClick={() => {
            this.setState((prevState) => {
              return {
                number: prevState.number + 1,
              };
            });
            //  this.setState   
            //    {} return ,
            // () return .
            this.setState((prevState) => ({
              number: prevState.number + 1,
            }));
          }}
        >
          +1
        </button>
      </div>
    );
  }
}

export default Counter4;
