import React from "react";
import PropTypes from "prop-types";

// PropTypes   
function MyComponent6({ name, children }) {
  return (
    <div>
      ,   {name}.
      <br />
      children  {children} .
    </div>
  );
}

MyComponent6.defaultProps = {
  name: " ",
};

MyComponent6.propTypes = {
  name: PropTypes.string,
};
export default MyComponent6;
