import React, { useEffect, useState } from "react";
import axios from "axios";
import styles from "./css/styles.module.css";

export function Weather() {
  const [location, setLocation] = useState({ lat: 0, lon: 0 });
  const [data, setData] = useState();
  const getGeolocation = () => {
    if ("geolocation" in navigator) {
      /*    */
      // console.log(navigator);
      navigator.geolocation.getCurrentPosition(async (position) => {
        setLocation({
          lat: position.coords.latitude,
          lon: position.coords.longitude,
        });
        await axios
          .get(
            `https://api.openweathermap.org/data/2.5/weather?lat=${position.coords.latitude}&lon=${position.coords.longitude}&appid=${process.env.REACT_APP_WEATHER_API}`
          )
          .then((res) => {
            console.log(res.data);
            setData(res.data);
          });
      });
    } else {
      return alert("  ");
    }
  };

  useEffect(() => {
    getGeolocation();
  }, []);
  return (
    <>
        <div>
          <p> : {location.lat}</p>
          <p> : {location.lon}</p>

          {data && data ? (
            <>
              <p> : {data.name}</p>
              <p> : {Math.round(data.main.feels_like - 273.15)}</p>
              <p> : {Math.round(data.main.temp - 273.15)}</p>
              <p> : {data.main.humidity}%</p>
              <p> : {data.weather[0].main}</p>
              <p>  : {data.weather[0].description}</p>
              {data.weather[0].icon}
            </>
          ) : (
            ""
          )}
        </div>
    </>
  );
}
