import { Component } from "react";

class Counter4 extends Component {
  // constructor   
  state = {
    number: 0,
    fixedNumber: 0,
  };
  render() {
    const { number, fixedNumber } = this.state;
    return (
      <div>
        <h1>{number}</h1>
        <h2>   : {fixedNumber}</h2>
        <button
          onClick={() => {
            // this.state 
            this.setState(
              {
                number: number + 1,
              },
              () => {
                console.log(" setState .");
                console.log(this.state);
              }
            );
          }}
        >
          +1
        </button>
      </div>
    );
  }
}

export default Counter4;
