import React, { useState } from "react";

function Say1() {
  const [message, setMessage] = useState("");
  const onClickEnter = () => setMessage("!");
  const onClickLeave = () => setMessage(" !");
  return (
    <div>
      <button onClick={onClickEnter}></button>
      <button onClick={onClickLeave}></button>
      <h1>{message}</h1>
    </div>
  );
}

export default Say1;
