import React from "react";

function MyComponent1(props) {
  const blackDog = new BlackDog();
  blackDog.bark();

  const whiteDog = new WhiteDog();
  whiteDog.bark();

  return <div>,   {props.name}.</div>;
}

export default MyComponent1;

function BlackDog() {
  this.name = "";
  return {
    name: "",
    //      this .
    bark: function () {
      console.log("BlackDog : " + this.name + ": !");
    },
  };
}

function WhiteDog() {
  this.name = "";
  return {
    name: "",
    //      this .
    bark: () => {
      console.log("WhiteDog : " + this.name + ": !");
    },
  };
}
